<template>
  <ion-page>
    <ion-header class="ion-no-border" :translucent="true">
      <ion-toolbar style="background-color: white; box-shadow: none">
        <ion-buttons slot="start">
          <ion-button @click="$router.go(-1)">
            <i class="fa fa-times" style="color: purple; font-size: medium" aria-hidden="true"></i>
          </ion-button>
        </ion-buttons>
        <ion-title class="text2" style="color: purple"> Fee </ion-title>
        <div slot="end">
          <img src="../../public/assets/black.svg" width="40" style="float: right; margin-right: 15px; opacity: 0.5" />
        </div>
      </ion-toolbar>
    </ion-header>

    <ion-content>
      <ion-header collapse="condense">
        <ion-toolbar>
          <ion-title size="large">{{ $route.params.id }}</ion-title>
        </ion-toolbar>
      </ion-header>
      <div style="display: flex; justify-content: center; margin-top: 10px"></div>
      <div style="margin: 30px; margin-top: 50px">
        <a href="upi://pay?pa=pokarnikesh-1@okaxis&amp;pn=network-king.in&amp;cu=INR&amp;am=500"
          @click="active = false">
          <ion-button v-if="!load" shape="round" style="width: 100%; --background: #800080; margin-top: 30px">
            Pay 500 INR
          </ion-button>
        </a>
        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating">UPI Ref. id</ion-label>
          <ion-input style="color: purple" v-model="ref" :type="text"></ion-input>
          <div class="err">{{ validation.firstError("ref") }}</div>
        </ion-item>
        <div style="font-size: smaller; margin-top: 5px; color: rgb(74, 71, 71);">Enter UPI ref. id carefully. If enter wrongly your payment will be declined.</div>

         <ion-button v-if="!load" :disabled="active" shape="round" @click="submit"
          style="width: 100%; --background: #800080; margin-top: 30px">
          Submit Request
        </ion-button>
        <ion-button v-if="load" disabled shape="round" style="width: 100%; --background: purple; margin-top: 30px">
          Loading...
        </ion-button>
        <div class="text2">
          <h6 style="color: plum;">Terms and Conditions </h6>
          <ol style="list-style: lower-roman; color: #800080;">
            <li style="margin:10px">There are 4 level (Including you) in this system.</li>
            <li style="margin:10px">500 INR is joining fee. after fee payment only you can refer to other</li>
            <li style="margin:10px">On joining of first level member (Next to you) you will get 100 points (1 Points =
              1INR).</li>
            <li style="margin:10px">On joining of second and third level member you get 50 points for each</li>
            <li style="margin:10px">You can only withdraw earned points</li>
            <li style="margin:10px">you can only withdraw 200 points at one time</li>
            <li style="margin:10px">Witdraw request should be accepted within 72 hours</li>
            <li style="margin:10px">More you refer more you earn</li>
          </ol>
        </div>
      </div>
    </ion-content>
  </ion-page>
</template>

<script lang="js">
  import { IonContent, IonItem, IonInput, IonPage, IonButton, IonLabel } from "@ionic/vue";
  // import axios from 'axios';
  import SimpleVueValidation from 'simple-vue-validator';
  // import md5 from "md5"; 
  const Validator = SimpleVueValidation.Validator;


  export default {
    components: {
      IonContent,
      IonPage,
      IonItem,
      IonLabel,
      IonButton,
      IonInput,
    },
    data() {
      return {
        ref: '',
        load: false,
        user: '',
        active: true,
      }
    },
    validators: {
      'ref': function (value) {
        // value = md5(value)
        return Validator.value(value).required();
      },
    },
    mounted() {
      const t = window.localStorage.getItem('a22user')
      this.user = JSON.parse(t)
    },
    methods: {
      async submit() {
        const valid = await this.$validate();
        if (valid) {
          this.load = true;
          const axios = require('axios');
          let data = {
            "user_id": this.$store.state.user.key,
            "ref": this.ref
          };

          let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: 'https://api.network-king.in/recharge-request',
            data: data
          };

          axios.request(config)
            .then((response) => {
              //console.log(JSON.stringify(response.data));
              this.$store.commit('user', response.data.data)
              this.$router.replace('/home')
            })
            .catch((error) => {
              console.log(error);
            });

        }
      }
    }
  };
</script>

<style>
  ion-input {
    text-align: center;

    color: purple;
