<template>
  <ion-page>
    <ion-content>
      <ion-header collapse="condense">
        <ion-toolbar style="background-color: white; box-shadow: none">
          <ion-buttons slot="start">
            <ion-button @click="$router.push('/help')">
             
            </ion-button>
          </ion-buttons>
          <ion-title class="text2" style="color: purple"> Fee </ion-title>
          <div slot="end">
            <img
              src="../../public/assets/black.svg"
              width="40"
              style="float: right; margin-right: 15px; opacity: 0.5"
            />
          </div>
        </ion-toolbar>
        <ion-toolbar>
          <ion-title size="large">{{ $route.params.id }}</ion-title>
        </ion-toolbar>
      </ion-header>
       <i  @click="$router.push('/help')"
                class="fa fa-question"
                style="color: purple; font-size: large; position: absolute; right: 0px; margin: 10px;"
                aria-hidden="true"
              ></i>
      <div
        style="justify-content: space-around; display: flex; margin-top: 15px"
      >
        <img src="../../public/assets/black.svg" width="200" />
      </div>
      <h1 class="text2" style="color: purple; margin-left: 25px">Register</h1>
      <div style="display: flex; justify-content: center; margin-top: 10px">
        <h6
          v-if="err"
          style="color: red; text-transform: capitalize; text-align: center"
        >
          {{ err }}
        </h6>
      </div>
      <div style="margin: 30px">
        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating"
            >Name</ion-label
          >
          <ion-input style="color: purple" v-model="form.name"></ion-input>
          <div class="err">{{ validation.firstError("form.name") }}</div>
        </ion-item>
        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating"
            >Mobile</ion-label
          >
          <ion-input
            inputmode="numeric"
            maxlength="10"
            type="text"
            style="color: purple"
            v-model="form.mobile"
          ></ion-input>
          <span
          style="
            color: gray;
            font-size: 10px;
            position: absolute;
            margin-top: 13px;
            right: 0px;
          "
          >Please use Whatsapp number, Activation link will be share on Whatsapp</span
        >
          <div class="err">{{ validation.firstError("form.mobile") }}</div>
        </ion-item>

        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating"
            >UPI</ion-label
          >
          <ion-input style="color: purple" v-model="form.upi"></ion-input>
          <span
            style="
              color: gray;
              font-size: 10px;
              position: absolute;
              margin-top: 13px;
              right: 0px;
            "
            >Please add proper UPI. It can not be changed once submit.</span
          >
          <div class="err">{{ validation.firstError("form.upi") }}</div>
        </ion-item>

        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating"
            >Password</ion-label
          >
          <ion-input
            style="color: purple"
            v-model="form.password"
            :type="shwPass1 ? 'text' : 'password'"
          ></ion-input>
          <div class="err">{{ validation.firstError("form.password") }}</div>
          <i
            :class="shwPass1 ? 'fa fa-eye-slash' : 'fa fa-eye'"
            @click="shwPass1 = !shwPass1"
            style="
              color: purple;
              position: absolute;
              right: 0px;
              margin-top: 20px;
              z-index: 100;
            "
            aria-hidden="true"
          ></i>
        </ion-item>

        <ion-item>
          <ion-label style="font-size: small; color: purple" position="floating"
            >Refer Code</ion-label
          >
          <ion-input
            style="color: purple"
            v-model="form.refer_by"
            :readonly="read"
          ></ion-input>
          <div class="err">{{ validation.firstError("form.refer_by") }}</div>
        </ion-item>

        <ion-item lines="none" class="text0">
          <span
            style="
              margin-left: 5px;
              color: purple;
              text-align: center;
              font-size: medium;
            "
          >
            By register you agree our
            <a
              style="color: purple; font-weight: bold; text-decoration: none"
              @click="$router.push('/privacy-policy')"
            >
              Privacy and Term</a
            >
          </span>
        </ion-item>

        <ion-button
          v-if="!load"
          shape="round"
          @click="submit"
          style="width: 100%; --background: purple; margin-top: 30px"
        >
          Register
        </ion-button>
        <ion-button
          v-if="load"
          disabled
          shape="round"
          style="width: 100%; --background: purple; margin-top: 30px"
        >
          Loading...
        </ion-button>

        <div style="text-align: center; margin: 25px">
          <hr style="background-color: purple; height: 1px" />
          <span
            style="
              margin-top: -22px;
              margin-left: -13px;
              position: absolute;
              background-color: white;
              padding: 5px;
              color: purple;
            "
            >OR</span
          >
        </div>
        <div class="text0" style="text-align: center; margin: 25px">
          <span style="color: purple"
            >Already have account?
            <a
              style="color: purple; font-weight: bold"
              @click="$router.replace('/login')"
            >
              Login to your account</a
            ></span
          >
        </div>
      </div>
    </ion-content>
  </ion-page>
</template>

<script lang="js">
  import {
    IonContent, IonItem, IonInput, IonPage, IonButton,  
  } from "@ionic/vue";
  import axios from 'axios';
  import SimpleVueValidation from 'simple-vue-validator';
  // import md5 from "md5";
  const Validator = SimpleVueValidation.Validator;


  export default {
    components: {
      IonContent,
      IonPage,
      IonButton,
      IonItem,
      IonInput,
    
    },
    data() {
      return {
        form: {
          name: "", mobile: "", upi: "", password: "", refer_by: ""
        },
        shwPass1: false,
        shwPass2: false,
        read:false,
        err:'',
        load:false,
      }
    },
    mounted() {
      if(window.refer!=undefined){
  this.form.refer_by = window.refer;
  this.read = true;
}
  
      const url = window.location.href
      if(url.split('?')[1]!=undefined)
      {
        this.form.refer_by = url.split('?')[1]
      }
      // const date = new Date();1
      // let day = (date.getDate() - 1).toString();
      // let month = (date.getMonth() + 1).toString();
      // const year = date.getFullYear()
      // if (day.length == 1) {
      //   day = '0' + day
      // }
      // if (month.length == 1) {
      //   month = '0' + month
      // }
      // this.maxDate = year.toString() + "-" + month.toString() + "-" + day.toString();
    },
    validators: {
      'form.name': function (value) {
        return Validator.value(value).required()
      },
      'form.mobile': function (value) {
        return Validator.value(value).required().length(10);
      }, 
      'form.upi': function (value) {
        return Validator.value(value).required();
      },
      'form.refer_by': function (value) {
        return Validator.value(value).length(12);
      },
      'form.password': function (value) {
        return Validator.value(value).required().minLength(6).maxLength(16);
      },
    },
    methods: {
      async submit() {
        const valid = await this.$validate();
        if (valid) {
          this.load=true;
          var config = {
            method: 'post',
            url: 'https://api.network-king.in/register',
            data: this.form
          };
          axios(config)
            .then(() => {
              this.load=false;
              //console.log(res);
              this.$router.replace('/login')
            }).catch((e)=>{
              this.err = e.response.data.msg;
              setTimeout(() => {
                  this.err = '';
                  this.form.mobile = '';
              }, 3000);
              this.load=false;
            })
        }
      },
      
  
    }
  };
</script>

