<script lang="js">
    // import { IonContent, IonInput, IonPage, IonButton, IonGrid, IonRow, IonCol,
    //  IonRadioGroup, IonRadio } from "@ionic/vue";
      import { IonContent, IonPage, IonButton, IonGrid, IonRow, IonCol,
     } from "@ionic/vue";
    // import SimpleVueValidation from 'simple-vue-validator';
    // const Validator = SimpleVueValidation.Validator;
    import axios from 'axios';

    export default {
        components: {
            IonContent,
            IonButton,
            IonPage,
          
            IonGrid, IonRow, IonCol,
           
        },
        data() {
            return {
                userData: {},
                form: {},
                limit: window.localStorage.getItem('limit'),
                emailErr: "",
                usernameErr: "",
                mobileErr: "",
                maxDate: '',
                tree:{},
                load: false,
                edit: false,
                req:{}
            }
        },

        async mounted() {

          axios.get('https://api.network-king.in/withdraw-user/' + this.$store.state.user.key).then(u => {
        console.log(u.data);
        this.req = u.data
        // this.$store.commit('user', u.data)
      })


          this.userData = this.$store.state.user;
          let us = []

          this.userData.refer_to.forEach(element => {
            us.push({code:element.code,k:this.$store.state.user.key});
          });
          this.$forceUpdate();
         this.tree[0] = [this.$store.state.user.key];
         if(us!=[]){
         this.tree[1] = us;
         this.getTree(us,2)  
          }
          this.$forceUpdate();

        

    // console.log(this.tree[2]);
    //       this.getTree(this.tree[2],3)
          
        },
        methods: {
          getTree(key,i){
            this.$forceUpdate();
            this.tree[i] = [];
            let trr = []
            key.forEach(async (k) => {
              axios.get('https://api.network-king.in/user/'+k.code).then((u)=>{
                let dt = u;    
                dt.data.refer_to.forEach(e => {
                   trr.push({code:e.code,k:dt.data.key})
                });
              
                this.tree[i]=trr;
                this.$forceUpdate();
                if(i+1<4){
                this.getTree(trr,i+1);}
                else{
                  this.load = true;
                }
                this.$forceUpdate();
             })
            });
             
          },
            makeReq(){
            const config = {
                method: 'get',
                url: 'https://api.network-king.in/withdraw-request/'+this.$store.state.user.key,
              };

              axios.request(config)
              .then((response) => {
                //console.log(JSON.stringify(response.data));
                this.$store.commit('user',response.data);
                this.userData = response.data  
              })
              .catch((error) => {
                console.log(error);
              });

            }
        }
    };
</script>

