<template>
  <ion-app style="max-width:500px; margin-left: auto; margin-right: auto; border-radius: 10px; border: 2px solid purple;">
    <ion-router-outlet />
  </ion-app>
</template>

<script lang="js">
import { IonApp, IonRouterOutlet,useBackButton, useIonRouter } from '@ionic/vue';

import { App } from '@capacitor/app';
import { defineComponent } from 'vue';
// import axios from "axios";

export default defineComponent({
  name: 'App',
  components: {
    IonApp,
    IonRouterOutlet
  },
  setup() {
    const ionRouter = useIonRouter();
    useBackButton(-1, () => {
      if (!ionRouter.canGoBack()) {
        App.exitApp();
      }
    });
  },
  data(){
    return{
      link:false
    }
  },
  mounted(){
    let a  = location.href
    a = a.split('?');
    if(a[1]!=undefined){
    a = a[1].split('=')[1]
    window.refer = a;
    this.$router.replace('/register')
  }

    // App.addListener('appUrlOpen',  async function (data) {
    //     const code = await data.url.split('?')
    //     this.link=true
      
    //     if(code[1]!=undefined){
    //       window.localStorage.clear();
    //         this.$router.replace('/register?'+code[1])
    //     }
    // });

  if(!this.link){
    // const user = JSON.parse(window.localStorage.getItem('a22user'))
    
    if(this.$store.state.user!=null){
      this.$router.replace('/home')
    }
    else{
      this.$router.replace('/login')
    }
  }
  
      
  }
});
</script>
