<script lang="js">
  import { IonContent, IonHeader, IonInput, IonButton, IonPage, IonTitle, IonToolbar, IonFab, IonFabButton, IonFabList } from '@ionic/vue';
  import { defineComponent } from 'vue';
  import axios from 'axios';
  import { Plugins } from '@capacitor/core';
  const { Share } = Plugins;

  // import { Clipboard } from '@ionic-native/clipboard';
  export default defineComponent({
    name: 'HomePage',
    components: {
      IonContent,
      IonHeader,
      IonPage,
