        of_load: false,
        wish: "",
        code: '',
        err:'',
      }
    },
    methods: {
      logout() {
        window.localStorage.clear();
        this.$router.replace('/login')
      },

      AccVrfy() {
        let key = this.$store.state.user.key;
        let code = this.code
        let c = code.split("").reverse().join("");
        if(c==key){
          axios.get('https://api.network-king.in/activemyaccount/'+this.code).then(d=>{
            this.$store.commit('user', d.data)
          })
        }
        else{
          this.err = "Invalid Code";
          setTimeout(() => {
            this.err = '';
            this.code = '';
          }, 3000);
        }
      },

      getWish() {
        const day = new Date();
        const hr = day.getHours();
        if (hr >= 0 && hr < 12) {
          return "Good Morning!"
        } else if (hr == 12) {
          return "Good Noon!"
        } else if (hr >= 12 && hr <= 15) {
          return "Good Afternoon!"
        } else {
          return "Good Evening!"
        }
      },
      copy() {
        navigator.clipboard.writeText("Sign Up with my code " + this.userData.key + " And get more points \n" + "Sign up with this https://network-king.in/?refer=" + this.userData.key);
        this.copyied = true;
        setTimeout(() => {
          this.copyied = false;
        }, 2000);
      },
      async share() {
        await Share.share({
          title: 'Refer Network-King With Buddies',
          text: "Sign Up with my code " + this.userData.key + " And get more points \n" + "Sign up with this https://network-king.in/?refer=" + this.userData.key,
          dialogTitle: 'Share with buddies',
        });
      }
    },
    mounted() {

      axios.get('https://api.network-king.in/user/' + this.$store.state.user.key).then(u => {
        //console.log(u.data);
        this.$store.commit('user', u.data)
      })

      if (this.$store.state.user.fee == 0) {
        this.$router.push('/fee')
      }
      // this.$router.push('/fee')

      var e = new Date();
      e = new Date(e).toLocaleString("en-US", { timeZone: 'Asia/Kolkata' }).split(',')[0];
      this.claimDate = e
      const day = new Date();
      const hr = day.getHours();
      if (hr >= 0 && hr < 12) {
        this.wish = "Good Morning!"
      } else if (hr == 12) {
        this.wish = "Good Noon!"
      } else if (hr >= 12 && hr <= 15) {
        this.wish = "Good Afternoon!"
      } else {
        this.wish = "Good Evening!"
      }
      this.userData = this.$store.state.user;
      //console.log(this.$store.state.user);
      this.claimBtn = this.userData.claim

      //  axios.get('https://refersystem-1-t6689342.deta.app/claim-now/'+this.userData.key).then(dd=>{
